        .contenu-article{
            display: flex;
            margin-top: 20px;
            min-width: 825px;
            margin-bottom: 100px;


            .click-article{
                position: relative;
                top: -200px;
                font-family: 'BebasNeue_Bold.otf';
                font-size: 1.3em;
                text-align: center;
            }

            img{
                width: 30%;
                height: 30%;
                object-fit: cover;
                transition: 1s;

                @media screen and (max-width: 890px) {
                    width: 100%;
                }

                &:hover{
                    filter : grayscale(100%);
                    cursor: pointer;
                    content: 'texte !';
                    transition: 1s;

                }
            }

            .contenaire-text{

                color: #FFFFFF;

                .titre-actu{
                    font-family: 'BebasNeue_Regular.otf';
                    font-size: 2em;
                    padding-left: 10px;
                }

                .text-actu{
                    padding-left: 10px;
                    font-family: 'Helvetica';
                }

                .croix-suppr-article{

                    a{
                        text-decoration: none;
                        color: rgb(126, 20, 23);
                        padding-left: 10px;
                    }

                }

                .show-article{

                    width: 250px;
                    height: auto;
                    color: #cb2025;
                    -webkit-border-radius: 28;
                    -moz-border-radius: 28;
                    border-radius: 28px;
                    padding: 10px 20px 10px 20px;
                    border: 1px solid #cb2025;
                    font-family: 'Helvetica';
                    text-align: center;
                    text-decoration: none;
                    position: relative;
                    top: 50px;
                    left: 15px;
                    transition: 1s;

                    &:hover{
                        text-decoration: none;
                        transition: 1s;
                        background: rgba(126, 20, 23, 0.5);
                    }

                }

            }

            @media screen and (max-width: 890px) {
                display: block;
                min-width: 0px;
            }

            @media screen and (min-width: 1100px) {
                max-width: 1100px;
            }
        }